body{
    background-color: white;
    font-family:monospace;
    font-size: 18px;

    margin: 0;
    overflow:auto;
}
div{
    color: #303030;
}
.divText{
    color: #606060
}
h1,h2,h3{
    color: black;
}
#header{
    /* background-color: #353333; */

    position: fixed;
    top: 0%;
    z-index: 1;
}
#header>*{
    padding: 5px 5% 5px 20px;
    width: calc(100vw - 20px - 5%);
    background-color: rgb(57, 105, 237);
    /* background-color: #353333; */

}
#header>.main{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    /* background-color: #353333; */
    height: 62px;
  
}
#header>.main>img{
    height: 4em;
}
.menuItems{
    padding-bottom: 15%!important;
    border-top: 1px white solid;
}
.hidden{
    opacity: 0;
    animation: hideDropdown 0.5s;
}
.visible{
    animation: showDropdown 0.5s;
}
@keyframes showDropdown{
    from{
        opacity:0;
        height: 0px;
    }
    to{
        opacity:1;
        height: auto;
    }
}
@keyframes hideDropdown{
    from{
        opacity:1;
        height: auto;
    }
    to{
        opacity:0;
        height: 0px;
    }
}
.menuItems>a{
    color:white;
    font-size: 18px;
    font-weight: bold;
    padding: 15px 5px ;
    border-bottom: 1px white solid;
    text-decoration: none;
    display: block;
    width:calc(100% - 20px)
}
#hero{
    height:100vh;
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    align-content: center;
    padding-top: 30%;

    background-size: cover;
    /* background-image: url('/static/img/background.jpg'); */
}

#hero>*>*{
    align-self: auto;
    color: white;
}
#hero>img{
    width:80%;
    padding: 20px;
}
.textblock{
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    align-content: center;
    padding: 5px;
    /* margin: 5px; */
}
.textblock>*{
    text-align: center;
}
.section{
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    padding: 5%;
}

.section>*{
    margin-top: 10px;
    margin-bottom: 20px;
}

.block{
    min-height: 20vh;
    /* width: 100vw; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    padding: 20px;

    text-align: center;
    font-weight: 500;
}

.anull{
    text-decoration: none;
}
@media only screen and (min-width: 768px) {

    #hero{
        height:100vh;
        display: flex;
        flex: 1;
        flex-direction: row;
        justify-content: space-evenly;
        align-items: center;
        align-content: center;
        padding-top: 0%;
    
    }
    .section{
        flex-direction: row;
        justify-content: space-evenly;
        align-items: flex-start;
        padding: 0px 15%;

    }

    /* body{
        padding: 0px 20%;
    } */

    #hero>img{
        height: 80%;
        width: auto;
        padding: 20px;
    }
}